.main-nav {
  color: $black;
  display: flex;
  align-items: center;
}

.main-nav__list {
  @include clear-list;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  @media (max-width: $tablet-max-width) {
    row-gap: 23px;
  }

  @media (max-width: $mobile-max-width) {
    display: none;
  }
}

.main-nav__list a {
  color: $black;

  &:hover {
    text-decoration: underline;
    text-decoration-color: $blue;
    text-underline-offset: 13px;
    text-decoration-thickness: 3px;
  }

  &:focus {
    color: $blue;
  }

  &:active {
    opacity: 0.7;
  }
}

.main-nav__phone {

  &:focus {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.4;
  }

  @media (max-width: $tablet-max-width) {
    white-space: nowrap;
  }
}

.main-nav__item:not(:last-of-type) {
  margin-right: 28px;

  @media (max-width: $desktop-container-width) {
    margin-right: 16px;
  }
} 

.main-nav__phone span {
  margin-left: 10px;
  vertical-align: super;

  &:hover {
    color: $dark-blue;
    transition-duration: 0.7s;
  }
}