.services__wrapper {
  margin-bottom: 50px;
}

.services__title {
  font-family: $font-title;
  font-size: 62px;
  font-weight: 600;

  @media (max-width: $tablet-max-width) {
    font-size: 48px;
  }
}

.services__list {
  @include clear-list;
}

.services__card {
  display: flex;
  width: 100%;
  margin-bottom: 50px;
  background-color: $lignt-gray;
  position: relative;

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
  }
}

.services__card:nth-child(even) .services__card-img {
  order:2;

  @media (max-width: $mobile-max-width) {
    order: 0;
  }
}

.services__card:nth-child(even) {
  justify-content: space-between;
}

.services__card-content {
  margin-left: 40px;
  margin-right: 40px;
  font-family: $font;
  font-size: 16px;
  font-weight: 300;
  padding-bottom: 20px;

  @media (max-width: $mobile-max-width) {
    margin: 10px;
  }
}

.services__card-content a {
  color: $blue;

  &:hover {
    color: $dark-blue;
    transition-duration: 0.7s;
  }
}

.services__card--close img {
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  height: 400px;
  width: 700px;
  object-fit:cover;


  @media (max-width: $desktop-container-width) {
    width: 450px;
    height: 355px;
    transform: translate(0,-0);
    top: 0;
    left: 0;
  }

  @media (max-width: $mobile-max-width) {
    width: 100%;
    height: auto;
  }
}

.services__card--close:nth-child(even) img {
  @media (max-width: $desktop-container-width) {
    right: 50%;
  }
}

.services__card--close .services__card-img {
  min-height: 400px;
  min-width: 600px;
  overflow: hidden;
  position: relative;
  order:0;

  @media (max-width: $desktop-container-width) {
    min-height: 300px;
    min-width: unset;
    width: 450px;
  }

  @media (max-width: $mobile-max-width) {
    width: 100%;
    height: 255px;
    min-height: unset;
  }
}

.services__card img {
  display: none;
}

.services__card--close img {
  display: block;
}

.services__card--close {
  &::before {
    content: "";
    width: 10px;
    height: 100%;
    position: absolute;
    right: 50%;
    top: 0;
    background-image: $gradient;

    @media (max-width: $desktop-container-width) {
      display: none;
    }
  }
}

.services__card--close:nth-child(even) {
  &::before {
    content: "";
    width: 10px;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
    background-image: $gradient;
  }
}

.services__card--close .services__card-text {
  display: none;
  transition-duration: 0.7s;
}



.services__card-title {
  font-family: $font;
  font-size: 48px;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 20px;

  @media (max-width: $desktop-container-width) {
    font-size: 44px;
    word-wrap: break-word;
  }

}

.services__card-button {
  color: $blue;
  font-family: $font;
  font-size: 24px;
  font-weight: 300;
  border: none;

  &:hover {
  color: $dark-blue;
  transition-duration: 0.7s;
  }

  &:active, &:focus {
    opacity: 0.7s;
  }
}

