.documents {
  margin-bottom: 80px;
}

.documents__title {
  font-family: $font-title;
  font-size: 62px;
  font-weight: 600;

  @media (max-width: $tablet-max-width) {
    font-size: 48px;
  }
}

.documents__list {
  @include clear-list;
  font-family: $font;
  font-size: 22px;
  font-weight: 400;
}

.documents_link {
  display: block;
  width: 100%;
  padding: 20px 30px;
  border-radius: 50px;
  background-color: $lignt-gray;
  margin-bottom: 30px;
  color: $black;

    &:hover {
      background-color: $blue;
      color: $white;
      transition-duration: 0.7s;
    }

    &:active {
      opacity: 0.5;
    }
}