.promo {
  display: flex;
  font-family: $font-title;
  padding-bottom: 400px;
  margin-top: 123px;

  @media (max-width: $mobile-max-width) {
    margin-top: 0;
    padding-bottom: 300px;
  }
}

.promo__wrapper {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: -290px;
    right: -296px;
    width: 921px;
    height: 921px;
    border-radius: 50%;
    color: red;
    background-image: $gradient;
    z-index: -2;

    @media (max-width: $desktop-container-width) {
      transform: scale(-1, 1);
    }

    @media (max-width: $mobile-max-width) {
      top: -108px;
      right: -180px;
      width: 760px;
      height: 760px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: -285px;
    right: -333px;
    width: 921px;
    height: 921px;
    border-radius: 50%;
    color: red;
    background-image: url("../img/promo.jpg");
    background-position: -224px 206px;
    background-repeat: no-repeat;
    background-size: auto;
    background-color: $gray;
    z-index: -1;

    @media (max-width: $desktop-container-width) {
      transform: scale(-1, 1);
    }

    @media (max-width: $mobile-max-width) {
      background-size: cover;
      background-position: -200px 106px;
      top: -128px;
      right: -185px;
      width: 760px;
      height: 760px;
    }
  }
}

.promo__title {
  font-size: 62px;
  font-weight: 600;
  margin-top: 180px;
  line-height: 0;

  @media (max-width: $desktop-container-width) {
    margin-top: 100px;
  }

  @media (max-width: $mobile-max-width) {
    font-size: 48px;
    line-height: normal;
  }
}

.promo__text {
  font-weight: 400;
  font-size: 29px;
  line-height: 25px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 83px;

  @media (max-width: $mobile-max-width) {
    line-height: normal;
  }
}

.promo__text span {
  color: $blue;
}

.promo__button {
  padding: 15px 35px;
  font-size: 30px;
  line-height: 18px;
  color: $white;
  border: none;
  background-color: $blue;
  box-shadow: 0 2px 3px 3px $shadow;
  border-radius: 23px;

  &:hover {
    background-color: $dark-blue;
    transition-duration: 0.6s;
  }

  &:focus {
    background-color: $dark-blue;
  }

  &:active {
    opacity: 0.7;
    transition-duration: 0.2s;
  }
}