.page-footer {
  background-color: $lignt-gray;
  flex: 0 0 auto;
}

.page-footer__title {
  font-family: $font-title;
  font-size: 62px;
  font-weight: 600;
  line-height: 50px;

  @media (max-width: $tablet-max-width) {
    font-size: 48px;
  }
}

.page-footer__phone-list {
  @include clear-list;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: auto auto;
  font-size: 20px;

  @media (max-width: $tablet-max-width) {
    grid-template-columns: auto;
  }
}

.page-footer__phone-item a {
  color: $black;

  &:hover {
    color: $blue;
  }

  &:active {
    opacity: 0.5;
  }
}

.page-footer__phone-title {
  position: relative;
  padding-left: 48px;
  font-size: 24px;

  &::before {
    content: "";
    width: 46px;
    height: 46px;
    position: absolute;
    top: -9px;
    left:-6px;
    background-image: url("../img/tel.png");
    background-repeat: no-repeat;
  }
}

.social__list {
  @include clear-list;
  line-height: 21px;
  margin-left: -10px;
  margin-top: 10px;
  letter-spacing: 0.2px;
  display: flex;

  @media (max-width: $tablet-max-width) {
    justify-content: center;
  }
}

.social__item img {
  @media (max-width: $tablet-max-width) {
    width: 60px;
    height: 60px;
  }
}

.page-footer__phone-item {
  margin-bottom: 20px;
}

.social__item {
  &:hover {
    opacity: 0.5;
  }
}

.social__item:not(:last-of-type) {
  margin-right: 20px;
}

.page-footer__contacts-list {
  @include clear-list;
  font-family: $font-title;
  color: $black;
  font-weight: 600;
  font-size: 20px;
  margin-top: 50px;
  margin-bottom: 50px;

  @media (max-width: $tablet-max-width) {
    font-size: 18px;
  }
}

.page-footer__contacts-item {
  margin-bottom: 30px;
  position: relative;
}

.page-footer__contacts-item a {
  color: $black;
  padding-left: 60px; 

  &:hover {
    color: $blue;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $tablet-max-width) {
    padding-left: 45px; 
  }
}

