@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto-light.woff2") format("woff2"), url("../fonts/roboto-light.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/roboto-black.woff2") format("woff2"), url("../fonts/roboto-black.woff") format("woff");
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  color: #1d1613;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

span {
  color: #42C8F2;
}

@media (max-width: 1023px) {
  .lock-menu {
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1000;
  }
}

.lock {
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1000;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 34px;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.03em;
}

.page-header {
  min-height: 122px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 5px 4px 1px rgba(0, 0, 0, 0.15);
  position: fixed;
  z-index: 100;
}
@media (max-width: 1220px) {
  .page-header {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .page-header {
    position: relative;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 1220px;
  padding: 25px 0 22px 0;
}
@media (max-width: 1220px) {
  .page-header__wrapper {
    min-width: unset;
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    flex-direction: column;
  }
}

.page-header__logo {
  font-weight: normal;
  line-height: 18px;
  color: #000000;
  cursor: default;
}
@media (max-width: 767px) {
  .page-header__logo {
    text-align: center;
  }
}

.page-header__logo:first-letter {
  color: #42C8F2;
  font-weight: 900;
}

.page-footer {
  background-color: #F2F2F2;
  flex: 0 0 auto;
}

.page-footer__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 62px;
  font-weight: 600;
  line-height: 50px;
}
@media (max-width: 1023px) {
  .page-footer__title {
    font-size: 48px;
  }
}

.page-footer__phone-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: auto auto;
  font-size: 20px;
}
@media (max-width: 1023px) {
  .page-footer__phone-list {
    grid-template-columns: auto;
  }
}

.page-footer__phone-item a {
  color: #000000;
}
.page-footer__phone-item a:hover {
  color: #42C8F2;
}
.page-footer__phone-item a:active {
  opacity: 0.5;
}

.page-footer__phone-title {
  position: relative;
  padding-left: 48px;
  font-size: 24px;
}
.page-footer__phone-title::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  top: -9px;
  left: -6px;
  background-image: url("../img/tel.png");
  background-repeat: no-repeat;
}

.social__list {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 21px;
  margin-left: -10px;
  margin-top: 10px;
  letter-spacing: 0.2px;
  display: flex;
}
@media (max-width: 1023px) {
  .social__list {
    justify-content: center;
  }
}

@media (max-width: 1023px) {
  .social__item img {
    width: 60px;
    height: 60px;
  }
}

.page-footer__phone-item {
  margin-bottom: 20px;
}

.social__item:hover {
  opacity: 0.5;
}

.social__item:not(:last-of-type) {
  margin-right: 20px;
}

.page-footer__contacts-list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-weight: 600;
  font-size: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .page-footer__contacts-list {
    font-size: 18px;
  }
}

.page-footer__contacts-item {
  margin-bottom: 30px;
  position: relative;
}

.page-footer__contacts-item a {
  color: #000000;
  padding-left: 60px;
}
.page-footer__contacts-item a:hover {
  color: #42C8F2;
}
.page-footer__contacts-item a:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .page-footer__contacts-item a {
    padding-left: 45px;
  }
}

