.contacts__item--mail::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  top: -9px;
  background-image: url("../img/mail.png");
  background-position: 0 -10px;
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .contacts__item--mail::before {
    width: 36px;
    height: 36px;
    background-position: 0 -8px;
    background-size: cover;
    top: -10px;
  }
}

.contacts__item--address::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  top: -9px;
  background-image: url("../img/address.png");
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .contacts__item--address::before {
    width: 36px;
    height: 36px;
    background-position: 0 0;
    background-size: cover;
    top: -10px;
  }
}

.page-footer__info {
  font-size: 14px;
  color: #ffffff;
  background-color: #9b9997;
  padding: 10px 0;
}

.page-main {
  flex: 1 0 auto;
}

.main-nav {
  color: #000000;
  display: flex;
  align-items: center;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-nav__list {
    row-gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    display: none;
  }
}

.main-nav__list a {
  color: #000000;
}
.main-nav__list a:hover {
  text-decoration: underline;
  text-decoration-color: #42C8F2;
  text-underline-offset: 13px;
  text-decoration-thickness: 3px;
}
.main-nav__list a:focus {
  color: #42C8F2;
}
.main-nav__list a:active {
  opacity: 0.7;
}

.main-nav__phone:focus {
  opacity: 0.5;
}
.main-nav__phone:active {
  opacity: 0.4;
}
@media (max-width: 1023px) {
  .main-nav__phone {
    white-space: nowrap;
  }
}

.main-nav__item:not(:last-of-type) {
  margin-right: 28px;
}
@media (max-width: 1220px) {
  .main-nav__item:not(:last-of-type) {
    margin-right: 16px;
  }
}

.main-nav__phone span {
  margin-left: 10px;
  vertical-align: super;
}
.main-nav__phone span:hover {
  color: #5799F7;
  transition-duration: 0.7s;
}

.container {
  width: 1220px;
  margin: auto;
}
@media (max-width: 1220px) {
  .container {
    width: unset;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: 22px;
    padding-right: 22px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.promo {
  display: flex;
  font-family: "Open Sans", "Arial", sans-serif;
  padding-bottom: 400px;
  margin-top: 123px;
}
@media (max-width: 767px) {
  .promo {
    margin-top: 0;
    padding-bottom: 300px;
  }
}

.promo__wrapper {
  position: relative;
}
.promo__wrapper::before {
  content: "";
  position: absolute;
  top: -290px;
  right: -296px;
  width: 921px;
  height: 921px;
  border-radius: 50%;
  color: red;
  background-image: linear-gradient(#42C8F2, #5799F7);
  z-index: -2;
}
@media (max-width: 1220px) {
  .promo__wrapper::before {
    transform: scale(-1, 1);
  }
}
@media (max-width: 767px) {
  .promo__wrapper::before {
    top: -108px;
    right: -180px;
    width: 760px;
    height: 760px;
  }
}
.promo__wrapper::after {
  content: "";
  position: absolute;
  top: -285px;
  right: -333px;
  width: 921px;
  height: 921px;
  border-radius: 50%;
  color: red;
  background-image: url("../img/promo.jpg");
  background-position: -224px 206px;
  background-repeat: no-repeat;
  background-size: auto;
  background-color: #9b9997;
  z-index: -1;
}
@media (max-width: 1220px) {
  .promo__wrapper::after {
    transform: scale(-1, 1);
  }
}
@media (max-width: 767px) {
  .promo__wrapper::after {
    background-size: cover;
    background-position: -200px 106px;
    top: -128px;
    right: -185px;
    width: 760px;
    height: 760px;
  }
}

.promo__title {
  font-size: 62px;
  font-weight: 600;
  margin-top: 180px;
  line-height: 0;
}
@media (max-width: 1220px) {
  .promo__title {
    margin-top: 100px;
  }
}
@media (max-width: 767px) {
  .promo__title {
    font-size: 48px;
    line-height: normal;
  }
}

.promo__text {
  font-weight: 400;
  font-size: 29px;
  line-height: 25px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 83px;
}
@media (max-width: 767px) {
  .promo__text {
    line-height: normal;
  }
}

.promo__text span {
  color: #42C8F2;
}

.promo__button {
  padding: 15px 35px;
  font-size: 30px;
  line-height: 18px;
  color: #ffffff;
  border: none;
  background-color: #42C8F2;
  box-shadow: 0 2px 3px 3px rgba(0, 0, 0, 0.15);
  border-radius: 23px;
}
.promo__button:hover {
  background-color: #5799F7;
  transition-duration: 0.6s;
}
.promo__button:focus {
  background-color: #5799F7;
}
.promo__button:active {
  opacity: 0.7;
  transition-duration: 0.2s;
}

.about {
  display: flex;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #F2F2F2;
}

.about__wrapper {
  padding-left: 590px;
  padding-bottom: 10px;
  background-image: url("../img/about.jpg");
  background-position: -28px 0;
  background-repeat: no-repeat;
  background-size: auto;
}
@media (max-width: 1220px) {
  .about__wrapper {
    background-position: -10% 50%;
  }
}
@media (max-width: 1023px) {
  .about__wrapper {
    background-position: 50% 0;
    padding-left: 20px;
    padding-top: 380px;
  }
}

.about__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 60px;
  font-weight: 600;
  line-height: 20px;
  margin-top: 44px;
}
@media (max-width: 1023px) {
  .about__title {
    font-size: 48px;
  }
}

.about__text {
  font-size: 18px;
  line-height: 25px;
}

.services__wrapper {
  margin-bottom: 50px;
}

.services__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 62px;
  font-weight: 600;
}
@media (max-width: 1023px) {
  .services__title {
    font-size: 48px;
  }
}

.services__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.services__card {
  display: flex;
  width: 100%;
  margin-bottom: 50px;
  background-color: #F2F2F2;
  position: relative;
}
@media (max-width: 767px) {
  .services__card {
    flex-direction: column;
  }
}

.services__card:nth-child(even) .services__card-img {
  order: 2;
}
@media (max-width: 767px) {
  .services__card:nth-child(even) .services__card-img {
    order: 0;
  }
}

.services__card:nth-child(even) {
  justify-content: space-between;
}

.services__card-content {
  margin-left: 40px;
  margin-right: 40px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  padding-bottom: 20px;
}
@media (max-width: 767px) {
  .services__card-content {
    margin: 10px;
  }
}

.services__card-content a {
  color: #42C8F2;
}
.services__card-content a:hover {
  color: #5799F7;
  transition-duration: 0.7s;
}

.services__card--close img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 400px;
  width: 700px;
  object-fit: cover;
}
@media (max-width: 1220px) {
  .services__card--close img {
    width: 450px;
    height: 355px;
    transform: translate(0, 0);
    top: 0;
    left: 0;
  }
}
@media (max-width: 767px) {
  .services__card--close img {
    width: 100%;
    height: auto;
  }
}

@media (max-width: 1220px) {
  .services__card--close:nth-child(even) img {
    right: 50%;
  }
}

.services__card--close .services__card-img {
  min-height: 400px;
  min-width: 600px;
  overflow: hidden;
  position: relative;
  order: 0;
}
@media (max-width: 1220px) {
  .services__card--close .services__card-img {
    min-height: 300px;
    min-width: unset;
    width: 450px;
  }
}
@media (max-width: 767px) {
  .services__card--close .services__card-img {
    width: 100%;
    height: 255px;
    min-height: unset;
  }
}

.services__card img {
  display: none;
}

.services__card--close img {
  display: block;
}

.services__card--close::before {
  content: "";
  width: 10px;
  height: 100%;
  position: absolute;
  right: 50%;
  top: 0;
  background-image: linear-gradient(#42C8F2, #5799F7);
}
@media (max-width: 1220px) {
  .services__card--close::before {
    display: none;
  }
}

.services__card--close:nth-child(even)::before {
  content: "";
  width: 10px;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  background-image: linear-gradient(#42C8F2, #5799F7);
}

.services__card--close .services__card-text {
  display: none;
  transition-duration: 0.7s;
}

.services__card-title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 48px;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 20px;
}
@media (max-width: 1220px) {
  .services__card-title {
    font-size: 44px;
    word-wrap: break-word;
  }
}

.services__card-button {
  color: #42C8F2;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 300;
  border: none;
}
.services__card-button:hover {
  color: #5799F7;
  transition-duration: 0.7s;
}
.services__card-button:active, .services__card-button:focus {
  opacity: 0.7s;
}

.specialistst__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 62px;
  font-weight: 600;
}
@media (max-width: 1023px) {
  .specialistst__title {
    font-size: 48px;
  }
}

.specialists__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: "Roboto", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .specialists__list {
    justify-content: space-around;
    gap: 20px;
  }
}

.specialists__item {
  width: 273px;
  margin-right: 35px;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .specialists__item {
    margin-right: 0;
  }
}

.specialists__item {
  position: relative;
}
.specialists__item::before {
  content: "";
  width: 273px;
  height: 10px;
  position: absolute;
  top: 273px;
  background-image: linear-gradient(30deg, #42C8F2, #5799F7);
}

.specialists_last-name {
  font-size: 46px;
  font-weight: bold;
  margin-bottom: 10px;
  margin-top: 20px;
}

.specialists_name {
  font-size: 22px;
  font-weight: bold;
  margin-top: 10px;
  line-height: 18px;
}

.specialists_position {
  font-weight: 300;
  font-size: 22px;
}

.documents {
  margin-bottom: 80px;
}

.documents__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 62px;
  font-weight: 600;
}
@media (max-width: 1023px) {
  .documents__title {
    font-size: 48px;
  }
}

.documents__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
}

.documents_link {
  display: block;
  width: 100%;
  padding: 20px 30px;
  border-radius: 50px;
  background-color: #F2F2F2;
  margin-bottom: 30px;
  color: #000000;
}
.documents_link:hover {
  background-color: #42C8F2;
  color: #ffffff;
  transition-duration: 0.7s;
}
.documents_link:active {
  opacity: 0.5;
}

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  margin: auto;
  z-index: 100;
  display: none;
}

.modal--open {
  display: block;
}

.modal--clear {
  display: block;
  position: static;
}

.modal--open .modal__close-button {
  position: absolute;
  top: 19px;
  right: 18px;
  width: 20px;
  height: 20px;
  border: 0;
  cursor: pointer;
  background-color: transparent;
}
.modal--open .modal__close-button:focus {
  opacity: 0.5;
}
@media (max-width: 767px) {
  .modal--open .modal__close-button {
    right: 11px;
    top: 11px;
    width: 19px;
  }
}

.modal--open .modal__close-button::before,
.modal--open .modal__close-button::after {
  content: "";
  width: 26px;
  height: 3px;
  position: absolute;
  top: 10px;
  left: -4px;
  background-color: #000000;
}
@media (max-width: 767px) {
  .modal--open .modal__close-button::before,
  .modal--open .modal__close-button::after {
    width: 16px;
    height: 2px;
    top: 11px;
    right: 0;
    left: 0;
  }
}

.modal--open .modal__close-button::before {
  transform: rotate(45deg);
}

.modal--open .modal__close-button::after {
  transform: rotate(-45deg);
}

.modal__overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  overflow: scroll;
}

