.about {
  display: flex;
  font-family: $font;
  background-color: $lignt-gray;

}

.about__wrapper {
  padding-left: 590px;
  padding-bottom: 10px;
  background-image: url("../img/about.jpg");
  background-position: -28px 0;
  background-repeat: no-repeat;
  background-size: auto;

  @media (max-width: $desktop-container-width) {
    background-position: -10% 50%;
  }

  @media (max-width: $tablet-max-width) {
    background-position: 50% 0;
    padding-left: 20px;
    padding-top: 380px
  }
}

.about__title {
  font-family: $font-title;
  font-size: 60px;
  font-weight: 600;
  line-height: 20px;
  margin-top: 44px;

  @media (max-width: $tablet-max-width) {
    font-size: 48px;
  }
}

.about__text {
  font-size: 18px;
  line-height: 25px;
}
