.page-header {
  min-height: 122px;
  font-family: $font;
  font-size: 20px;
  width: 100%;
  background-color: $white;
  box-shadow: 0 5px 4px 1px $shadow;
  position: fixed;
  z-index: 100;

  @media (max-width: $desktop-container-width) {
    font-size: 16px;
  }

  @media (max-width: $mobile-max-width) {
    position: relative;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: $desktop-container-width;
  padding: 25px 0 22px 0;

  @media (max-width: $desktop-container-width) {
    min-width: unset;
  }

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
  }
}

.page-header__logo {
  font-weight: normal;
  line-height: 18px;
  color: $black;
  cursor: default;

  @media (max-width: $mobile-max-width) {
    text-align: center;
  }
}

.page-header__logo:first-letter {
  color: $blue;
  font-weight: 900;
}