.specialistst__title {
  font-family: $font-title;
  font-size: 62px;
  font-weight: 600;

  @media (max-width: $tablet-max-width) {
    font-size: 48px;
  }
}

.specialists__list {
  @include clear-list;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: $font;

  @media (max-width: $tablet-max-width) {
    justify-content: space-around;
    gap: 20px;
  }
}

.specialists__item {
  width: 273px;
  margin-right: 35px;
  margin-bottom: 20px;

  @media (max-width: $tablet-max-width) {
    margin-right: 0;
  }
}

.specialists__item {
  position: relative;

  &::before {
    content: "";
    width: 273px;
    height: 10px;
    position: absolute;
    top: 273px;
    background-image: $gradient-gorizontal;
  }
}

.specialists_last-name {
  font-size: 46px;
  font-weight: bold;
  margin-bottom: 10px;
  margin-top: 20px;
}

.specialists_name {
  font-size: 22px;
  font-weight: bold;
  margin-top: 10px;
  line-height: 18px;
}

.specialists_position {
  font-weight: 300;
  font-size: 22px;
}